@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sacramento:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
.course-box-card{display:flex;opacity:1;flex-direction:column;box-shadow:0px 4px 4px rgba(51,51,51,0.04),0px 4px 16px rgba(51,51,51,0.08);border-radius:8px;overflow:hidden;position:relative}.course-box-card--disabled{background-color:var(--accent 0.1);opacity:0.5}.course-box-card:hover{-webkit-transform:scale(0.992);transform:scale(0.992);box-shadow:0px 0px 3px 1px rgba(51,51,51,0.056)}.course-box-card__image::after{position:absolute;content:"";width:100%;height:100%;background:linear-gradient(var(--black), transparent 30%);opacity:0.5}

.courses-row-layout,.card-grid-layout{padding-bottom:10px;scroll-snap-type:x proximity;scroll-behavior:smooth;-webkit-overflow-scrolling:touch}.courses-row-layout .course-box-card,.card-grid-layout .course-box-card{scroll-snap-align:center}

:root{--primary-base: #660066;--primary-hover: #660066b2;--secondary-base: #800020;--secondary-05: #ffc5d336;--secondary-1: #ffc5d3;--secondary-2: #ff93ae;--secondary-3: #fc5780;--secondary-4: #dd3a63;--secondary-5: #bd1f46;--secondary-6: #9b203f;--secondary-7: #5a051a;--secondary-8: #3c0311;--secondary-9: #1d060b;--accent-1: #e8e8e8;--accent-2: #c1c9d2;--accent-3: #697386;--accent-4: #7fffdf;--white: #ffffff;--black: #000000}*{scroll-behavior:smooth}*::-webkit-scrollbar{width:6px;height:6px}*::-webkit-scrollbar-thumb{background-color:#660066;background-color:var(--primary-base);border-radius:10px}*::-webkit-scrollbar-track{border-radius:10px;background-color:#ffc5d336;background-color:var(--secondary-05)}.disabled-link{pointer-events:none}.date-time-picker .MuiInputBase-root,.date-time-picker .MuiFormControl-root,.date-time-picker input{width:100% !important}.date-time-picker .MuiInputBase-root::after,.date-time-picker .MuiInputBase-root::before{content:none !important;border-bottom:none !important}.date-time-picker input{height:40px !important;border:1px solid #c1c9d2 !important;border:1px solid var(--accent-2) !important;border-radius:2px !important;padding:0 16px !important}.date .MuiInputBase-input.MuiInput-input{border:none !important}.rec.rec-arrow{border-radius:0;background-color:white;border:none;box-shadow:none}.rec.rec-arrow:hover:enabled{background-color:white;color:black;box-shadow:none}.rec .rec-arrow:disabled{visibility:hidden}.rec .rec-dot_active:hover{box-shadow:0 0 1px 3px #333;background-color:#333}.rec .rec-dot:hover{box-shadow:0 0 1px 3px #333}.rec .rec-dot:focus{cursor:pointer;box-shadow:0 0 1px 3px #333}.rec .rec-dot_active{box-shadow:0 0 1px 3px #333;background-color:#333}.relative{position:relative}

.user-header-nav-link,.user-courses-nav-link{display:flex;align-items:center;height:100%}.user-header-nav-link{padding:0 16px;border-bottom:2px solid transparent}.user-header-nav-link--active{color:var(--primary-base);border-color:var(--primary-bases)}.user-courses-nav-link{padding:0 10px;color:var(--accent-3)}.user-courses-nav-link--active{color:var(--black)}

.user-forum-sidebar-link{padding:10px;padding-left:24px;display:flex;align-items:center}.user-forum-sidebar-link:hover{background-color:var(--secondary-05)}.user-forum-sidebar-link--active{color:var(--white);background-color:var(--primary-base)}.user-forum-sidebar-link--active:hover{background-color:var(--primary-hover)}

.mini .Root-container-1{width:500px;height:400px !important}

.take-lesson-video-wrapper video::-webkit-media-controls-timeline{display:none}.take-lesson-video-wrapper video::-webkit-media-controls-fullscreen-button{position:relative;z-index:100;-webkit-transform:translateX(100%);transform:translateX(100%)}

.hidden {
  display: block;
}

@media screen and (max-width: 500px) {
  .side-bar {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: 0.5s;
  }
  .hidden {
    display: none;
  }
}
@tailwind base;
@tailwind components;
@tailwind utilities;
